.container {
    max-width: 144rem;
    padding: 6.5rem 10rem 22.5rem 10rem;
    margin: 0 auto;
}

.header {
    display: flex;
    align-items: center;
}

.navButton {
    width: 5rem;
    height: 2.2rem;
    display: none;
    cursor: pointer;
}

.navline {
    width: 5rem;
    height: .4rem;
    transition: all .2s ease;

    &.blue {
        transform-origin: left;
    }

    &.purple {
        transform-origin: right;
    }
}

.logoHolder {
    margin-right: auto;
}

.logoImage {
    height: 5rem;
    width: auto;
}

.linksHolder {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;

    &-mobile {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 5rem;
        list-style: none;
        transition: all .2s ease;
    }
}

.linksMobile {
    max-height: 0;
    display: none;
}

.navOn {
    max-height: 14rem;
    display: block;
}

.listItem {
    transition: all .2s ease;
    &:hover {
        transform: translateY(-2px);
        text-shadow: rgba(0,0,0,0.1) 0 1px 5px;
    }

    &-mobile {
        transition: all .2s ease;

        &:not(:last-child) {
            margin-bottom: 2rem;
        }

        &:hover {
            transform: translateY(-2px);
            text-shadow: rgba(0,0,0,0.1) 0 1px 5px;
        }
    }
}

.link {
    text-decoration: none;
    font-size: 1.8rem;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 15.5%;
}

.margin {
    margin-right: 5rem;
}

// main

.main {
    max-width: 100%;
    position: relative;
    margin-top: 6.8rem;
    outline: .4rem solid $purple;
    max-height: 75rem;
}

.slider {
    height: 61rem;
    background-image: url("./images/3x3---full.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: 50%;
}

.slide {
    &-1 {
        max-width: 100%;
        height: auto;
        
    }

    &-title {
        width: 42rem;
        height: 11rem;
        background-color: $white;
        position: absolute;
        z-index: 3;
        bottom: 0;
        right: 0;
        outline: .4rem solid $purple;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &-controls {
        width: 42rem;
        height: 11rem;
        background-color: $white;
        position: absolute;
        z-index: 3;
        bottom: -11.35rem;
        right: 0;
        outline: .4rem solid $purple;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

    &-social {
        width: 22rem;
        height: 11rem;
        background-color: $white;
        position: absolute;
        z-index: 3;
        bottom: -11.35rem;
        left: 0;
        outline: .4rem solid $purple;
        display: flex;
        align-items: center;
        justify-content: space-around;

        &-bar {
            width: .4rem;
            height: 100%;
            background-color: $purple;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

.iconsocial {
    font-size: 3.5rem;
    cursor: pointer;
    transition: all .2s ease;

    &:hover {
        transform: translateY(-2px);
        text-shadow: rgba(0,0,0,0.1) 0 1px 5px;
    }

    &-pink {
        color: $pink;
    }

    &-blue {
        color: $blue;
    }
}

.controls {
    &-back, &-fwd {
        font-size: 6rem;
        font-weight: 700;
        cursor: pointer;
        transition: all .2s ease;

        &:hover {
            transform: translateY(-2px);
            text-shadow: rgba(0,0,0,0.1) 0 1px 5px;
        }
    }

    &-back {
        color: $blue;
    }

    &-fwd {
        color: $pink;
    }

    &-bar {
        width: .4rem;
        height: 100%;
        background-color: $purple;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
    }
}

.arrow {
    max-width: 100%;
    height: auto;
    z-index: 3;
}

.o {
    width: 2.5rem;
    height: 2.5rem;
    position: absolute;
    z-index: 4;
}

.o {
    &-left {
        &-top {
            left: -1.25rem;
            top: -1.25rem;
        }

        &-bottom {
            left: -1.25rem;
            bottom: -1.25rem;

            &-social {
                &-1 {
                    left: -1.25rem;
                    bottom: -12.75rem;
                }

                &-2 {
                    left: 20.75rem;
                    bottom: -12.75rem;
                }

                &-3 {
                    left: 20.75rem;
                    bottom: -1.25rem;
                }
            }
        }
    }

    &-right {
        &-top {
            right: -1.25rem;
            top: -1.25rem;
        }

        &-bottom {
            right: -1.25rem;
            bottom: -1.25rem;

            &-text {
                right: -1.25rem;
                bottom: 10rem;

                &-2 {
                    right: 41rem;
                    bottom: 10rem;
                }

                &-3 {
                    right: 41rem;
                    bottom: -1.25rem;
                }
            }

            &-controls {
                &-1 {
                    bottom: -12.75rem;
                    right: -1.25rem;
                }

                &-2 {
                    bottom: -12.75rem;
                    right: 41rem;
                }
            }
        }
    }
}