@media only screen and (max-width: 1200px) {
    .slide {
        &-title {
            width: 100%;
        }
    }

    .o-right-bottom-text-2 {
        left: -1.25rem;
    }

    .navButton {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }

    .linksHolder {
        display: none;
    }
}

@media only screen and (max-width: 900px) {
    .slide {
        &-controls {
            width: 100%;
        }

        &-social {
            bottom: -22.7rem;
        }
    }

    .slider {
        background-position-y: 100%;
    }

    .container {
        padding-bottom: 30rem;
    }

    .o-left-bottom-social-3 {
        bottom: -24.1rem;
    }

    .o-right-bottom-controls-2 {
        bottom: -24.1rem;
        left: -1.25rem;
    }

    .o-right-bottom-text-3 {
        display: none;
    }
}

@media only screen and (max-width: 600px) {
    .container {
        padding-left: 7rem;
        padding-right: 7rem;
    }

    html {
        font-size: 50%;
    }

    .slider {
        background-position-y: -5rem;
    }
}

@media only screen and (max-width: 400px) {
    .slider {
        height: 45rem;
        background-position-y: -5rem;
    }
}

